@keyframes scroll{
  0% {
    transform: translate(0, 0);
  }

  100% {
    transform: translate(-100%, 0);
  }
}

.marquee {
  // margin-top: 50px;
  display: flex;
  align-items: center;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  height: 200px;
  background: $yellow-dark;
  border-top: 3px solid #000000;
  border-bottom: 3px solid #000000;
  position: relative;
  z-index: 101;

  &__img {
    display: inline-block;
    padding-left: 100%;
    animation: scroll 20s infinite linear;
    background-image: url('../img/pages/marquee.svg');
    // background-image: url(https://upload.wikimedia.org/wikipedia/commons/thumb/3/36/Hopetoun_falls.jpg/220px-Hopetoun_falls.jpg);
    width: 220px;
    height: 147px;
    margin-left: -5px;
    flex-shrink: 0;
  }

  &__wrap {
    margin-top: 50px;

    width: 100%;
    max-width: 100%;
    position: absolute;
    left: 0;
    top: 580px;
    opacity: 0;
    visibility: hidden;
    transition: 2s;
    z-index: 101;
    &.visible {
      opacity: 1;
      visibility: visible;
      transition: 2s;
    }

    @include width-960 {
      top: 660px;
    }
    @include width-768 {
      top: 550px;
    }
    // @include width-320 {
    //   width: auto;
    // }
  }
  &__place {
    height: 200px;
    margin-top: 50px;
  }
}
