.proj {
  margin-top: 80px;

  @include width-640 {
    margin-top: 60px;
  }
}

.proj-filter {
  margin-bottom: 60px;
  &__lists {
    display: flex;
    & >:last-child {
      margin-left: 200px;
    }
  }

  &__btns {
    display: flex;
    margin-bottom: 40px;
  }
  &__open {
    background: #FFE457;
    border: 2px solid #000000;
    border-radius: 40px;
    padding: 15px 20px;
    & span {
      font-weight: 500;
      font-size: 20px;
      line-height: 110%;
      color: #000000;
      position: relative;
      padding-left: 28px;
      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        margin: auto 0;
        width: 18px;
        height: 2px;
        background: #000000;
        background-size: 18px;
        transition: all 0.3s;
      }
      &::after {
        content: '';
        position: absolute;
        left: 8px;
        top: 0;
        bottom: 0;
        margin: auto 0;
        width: 2px;
        height: 18px;
        background: #000000;
        background-size: 18px;
        transition: all 0.3s;
      }
    }
    &.active {
      & span::after {
        transform: rotate(180deg);
        opacity: 0;
        transition: all 0.3s;
      }
      & span::before {
        transform: rotate(180deg);
        transition: all 0.3s;
      }
    }
  }
  &__clear {
    font-weight: 500;
    font-size: 20px;
    line-height: 110%;
    color: #000000;
    margin-left: 60px;
    position: relative;
    opacity: 0;
    visibility: hidden;
    &.active {
      opacity: 1;
      visibility: visible;
      transition: all 0.2s;
    }
    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 15px;
      width: 100%;
      height: 2px;
      background: #000000;
      transition: all 0.2s;
    }
    &:hover {
      &::after {
        opacity: 0;
        visibility: hidden;
      }
    }
  }

  &__lists {
    display: none;
    transition: all .3s;
    &.opened {
      display: flex;
      transition: all .3s;
    }
  }

  &__list {
    width: 100%;
    max-width: 340px;
    & ul {
      & li {
        display: block;
        width: fit-content;
        font-weight: 500;
        font-size: 20px;
        line-height: 110%;
        color: #000000;
        cursor: pointer;
        margin-top: 20px;
        position: relative;
        transition: all 0.2s;
        &::after {
          content: '';
          position: absolute;
          left: 0;
          bottom: -5px;
          width: 100%;
          height: 2px;
          background: #000000;
          opacity: 0;
          visibility: hidden;
          transition: 0.3s;
        }
        &:hover {
          &::after {
            opacity: 1;
            visibility: visible;
            transition: 0.3s;
          }
        }
        &.active {
          padding-left: 13px;
          &::before {
            content: '';
            position: absolute;
            left: 0;
            width: 8px;
            height: 8px;
            background: #FFE457;
            border: 2px solid #000000;
            border-radius: 50%;
            top: 0;
            bottom: 0;
            margin: auto 0;
          }
        }
      }
    }
  }

  &__title {
    display: block;
    font-weight: 500;
    font-size: 26px;
    line-height: 110%;
    color: #000000;
    margin-bottom: 30px;
  }
}

@keyframes filterHide {
  0% {
    visibility: visible;
    height: auto;
  }

  100% {
    visibility: hidden;
    width: 0;
    height: 0;
    margin: 0;
    font-size: 0;
  }
}

.filterHide {
  display: none;
  animation-name: filterHide;
  animation-duration: .4s;
  animation-fill-mode: forwards;
}

.filterAnimated {
  transform: scale(0);
  opacity: 0;
  transition: .4s;
}
