.faq {
  margin-top: 80px;

  @include width-640 {
    margin-top: 60px;
  }
  &__content {
  }

  &__list {
    margin-top: 40px;
    & ul {
      & li {
        font-weight: 600;
        font-size: 30px;
        line-height: 130%;
        color: #000000;
        cursor: pointer;
        padding: 30px 0;
        border-top: 2px solid rgba(0, 0, 0, 0.2);

        @include width-768 {
          font-size: 26px;
        }
        @include width-640 {
          @include scale(font-size,26,16);
        }
        &:last-child {
          border-bottom: 2px solid rgba(0, 0, 0, 0.2);
        }
        & span {
          display: block;
          width: 100%;
          position: relative;
          padding-right: 30px;
          &::after {
            content: '';
            position: absolute;
            right: 0;
            top: 0;
            bottom: 0;
            margin: auto 0;
            width: 20px;
            height: 20px;
            background: url('../img/icons/plus.svg') no-repeat;
            background-size: 20px;
            transition: 0.3s;
          }
          &.active {
            transition: 0.3s;
            &::after {
              transform: rotate(135deg);
              transition: 0.3s;
            }
          }
        }
      }
    }
  }

  &__more {
    max-width: 600px;
    max-height: 0;
    overflow: hidden;
    transition: max-height .3s ease-out;
    transition: 0.3s;
    &.active {
      margin-top: 20px;
      transition: 0.3s;
    }
  }
}
