.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  background: #ffffff;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
  &.active {
    opacity: 1;
    visibility: visible;
    transition: 0.3s;
  }
  &-close {
    position: absolute;
    right: 50px;
    top: 40px;
    & img {
      width: 60px;
      height: 60px;
    }

    @include width-768 {
      right: 10px;
      top: 10px;
    }
    @include width-640 {
      & img {
        width: 44px;
        height: 44px;
      }
    }
  }
  &-body {
    padding: 100px 0;
  }
}

.popup-clients {
  overflow-y: scroll;
  &__cards {
    margin-top: 40px;
    opacity: 0;
    visibility: hidden;
    transform: translateX(-60%);
    transition: 1s;
    &.active {
      opacity: 1;
      visibility: visible;
      transform: translateX(0);
      transition: 1s;
    }
  }
}

@keyframes opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.popup-req {
  overflow-y: scroll;

  &.active {
    & .popup-req__title {
      animation-name: opacity;
      animation-duration: 800ms;
      animation-timing-function: linear;
    }
  }
  &__title {
    font-weight: 600;
    font-size: 74px;
    line-height: 110%;
    color: #000000;
    text-transform: uppercase;
    max-width: 1000px;


    @include width-1250 {
      font-size: 64px;
    }
    @include width-768 {
      font-size: 44px;
    }
    @include width-640 {
      @include scale(font-size,44,28);
    }
  }
  &__file {
    margin-top: 40px;
  }
  &__form {
    max-width: 800px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    margin-top: 70px;
    opacity: 0;
    visibility: hidden;
    transform: translateX(-60%);
    transition: 1.2s;
    &.active {
      opacity: 1;
      visibility: visible;
      transform: translateX(0);
      transition: 1.2s;
    }

    @include width-640 {
      margin-top: 40px;
    }
  }
  &__input {
    padding: 10px 0;
    border: none;
    border-bottom: 2px solid #000000;
    font-weight: 600;
    font-size: 30px;
    line-height: 130%;
    color: #000000;
    margin-top: 30px;
    &::placeholder {
      font-weight: 600;
      font-size: 30px;
      line-height: 36px;
      color: rgba(0, 0, 0, 0.4);
    }

    @include width-768 {
      font-size: 26px;
      &::placeholder {
        font-size: 26px;
      }
    }
    @include width-640 {
      @include scale(font-size,26,20);
      &::placeholder {
        @include scale(font-size,26,20);
      }
    }
  }
  &__checkbox {
    margin-top: 20px;
  }
  &__btn {
    width: 100%;
    & button {
      width: 100%;
    }
  }
  &__btnwrap {
    padding: 0 10px;
  }
}

.checkbox {
  position: relative;
  &.disabled {
    & label {
      cursor: not-allowed;
    }
  }
  & input {
    position: absolute;
    z-index: -1;
    opacity: 0;
  }
  & label {
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: rgba(0, 0, 0, 0.6);
    cursor: pointer;
    position: relative;
    padding-left: 28px;
  }
  & input + label {
    display: inline-flex;
    align-items: center;
    user-select: none;
    &::before {
      content: '';
      display: inline-block;
      position: relative;
      width: 18px;
      height: 18px;
      flex-shrink: 0;
      flex-grow: 0;
      border: 2px solid rgba(0, 0, 0, 0.6);
      transition: 0.3s;
      cursor: pointer;
      position: absolute;
      left: 0;
      top: 2px;
    }
  }
  &-input {
    &:checked+label::before {
      background: url('../img/icons/checked.svg') no-repeat;
      background-size: 100% 100%;
      transition: 0.3s;
    }
    &:disabled+label::before {
      background-color: #e9ecef;
      opacity: 0.8;
      cursor: not-allowed;
    }
    &:focus-visible+label::before {
      outline: 1px solid #F2EDE6;
    }
  }
}


.input__wrapper {
  position: relative;

}

.input__file {
  opacity: 0;
  visibility: hidden;
  position: absolute;
}

.input__file-icon-wrapper {
  height: 60px;
  width: 60px;
  margin-right: 15px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  border-right: 1px solid #fff;
}

.input__file-button-text {
  margin-top: 1px;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  color: #000000;
  padding-left: 24px;
}

.input__file-button {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  cursor: pointer;
  &::after {
    content: '';
    position: absolute;
    width: 14px;
    height: 14px;
    left: 0;
    background: url('../img/icons/plus.svg') no-repeat;
    background-size: 14px;
  }
  &:focus {
    box-shadow: 0px 0px 0px 2px #000000;
  }
}



.choices {
  width: 100%;
  margin-bottom: 0;
  &__inner {
    border: none;
    background: none;
    border-radius: 0;
    font-weight: 600;
    font-size: 30px;
    line-height: 36px;
    color: rgba(0, 0, 0, 0.4);
    border-bottom: 2px solid #000000;
    // text-align: center;
    padding: 0;

    @include width-768 {
      font-size: 26px;
    }
  }
  &__list {
    &--single {
      padding: 0;
    }
  }
}

.choices[data-type*=select-one] .choices__inner {
  padding-bottom: 10px;
}

.choices__list--dropdown, .choices__list[aria-expanded] {
  margin-top: 10px;
  box-shadow: 0px 8px 24px rgba(150, 150, 150, 0.25);
  border-radius: 20px;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  color: rgba(0, 0, 0, 0.8);
  border: none;
}

.archive__select .choices__inner {
  background: #ffffff;
}

.archive__select .is-open .choices__list--dropdown {
  background: #ffffff;
}

.work-yes__select .choices__inner {
  background: #FFFFFF;
}

.work-yes__select .is-open .choices__list--dropdown {
  background: #FFFFFF;
}

.is-open .choices__inner {
  border-radius: 6px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.is-open .choices__list--dropdown {
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  color: rgba(0, 0, 0, 0.8);
  border-top: 2px solid rgba(0, 0, 0, 0.1);
}

.choices__list--dropdown .choices__item--selectable.is-highlighted::after, .choices__list[aria-expanded] .choices__item--selectable.is-highlighted::after {
  display: none;
}

.choices[data-type*=select-one]::after {
  border: none;
  width: 22px;
  height: 13px;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto 0;
  background: url('../img/icons/select_arrow.svg') no-repeat;
  background-size: 22px;
  transition: 0.4s;
}

.choices[data-type*=select-one].is-open::after {
  border: none;
  width: 22px;
  height: 13px;
  top: 0;
  bottom: 0;
  margin: auto 0;
  transform: rotateX(180deg);
  transition: 0.4s;
}

.choices__placeholder {
  opacity: 1;
}

.choices__placeholder[data-choice-selectable] {
  display: none;
}

.choices__list--dropdown .choices__item, .choices__list[aria-expanded] .choices__item {
  font-size: 18px;
  padding: 10px 0;
  margin: 0 30px;
  border-top: 2px solid rgba(0, 0, 0, 0.1);
  position: relative;
  &::before {
    content: "";
    position: absolute;
    background: $yellow-dark;
    z-index: -1;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    clip-path: inset(100% 0 0 0);
    transition: 0.2s linear;
  }
  &:hover {
    &::before {
      clip-path: inset(0 0 0 0);
    }
  }
  &:last-child {
    border-bottom: 2px solid rgba(0, 0, 0, 0.1);
  }
}

.choices__list--dropdown .choices__item--selectable, .choices__list[aria-expanded] .choices__item--selectable {
  padding-right:  0;
}

.choices__list--dropdown .choices__item--selectable.is-highlighted, .choices__list[aria-expanded] .choices__item--selectable.is-highlighted {
  background: $yellow-dark;
  transition: 0.8s;
}

.choices__list--dropdown .choices__list, .choices__list[aria-expanded] .choices__list {
  max-height: 250px;
  border: 2px solid #000000;
  border-radius: 20px;
  padding: 20px 0;
}
